/*-------------------------------------
		1.General css (Reset code)
--------------------------------------*/

* {
  padding: 0;
  margin: 0;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  position: relative;
  font-size: 14px;
  color: #222222;
  background: #fff;
  font-family: "Merriweather Sans", sans-serif;
  max-width: 1920px;
  margin: 0 auto;
  overflow-x: hidden;
}

a, a:hover, a:active, a:focus {
  display: inline-block;
  text-decoration: none;
  font-size: 13px;
  padding: 0;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  color: #222222;
  text-transform: capitalize;
}

p {
  margin: 0;
  color: #666666;
  font-size: 14px;
}

img {
  border: none;
  max-width: 100%;
  height: auto;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

ul li {
  list-style: none;
}

select, input, textarea, button {
  box-shadow: none;
  outline: 0 !important;
}

html, body {
  height: 100%;
  z-index: 2;
}

[placeholder]:focus::-webkit-input-placeholder {
  -webkit-transition: opacity 0.3s 0.3s ease;
  -moz-transition: opacity 0.3s 0.3s ease;
  -ms-transition: opacity 0.3s 0.3s ease;
  -o-transition: opacity 0.3s 0.3s ease;
  transition: opacity 0.3s 0.3s ease;
  opacity: 0;
}

/*=============Style css=========*/

/*-------------------------------------
		2. Header
--------------------------------------*/

.top-area {
  position: absolute;
  width: 100%;
}

.wrapper {
  position: relative;
  z-index: -1;
}

.header-area {
  position: relative;
  width: 100%;
}

/*.sticky-wrapper */

.sticky-wrapper {
  position: relative;
  z-index: 2;
  transition: 0.7s;
}

.is-sticky .header-area {}

.is-sticky .header-area:after {
  position: absolute;
  content: "";
  background: rgba(77, 78, 84, 1);
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-transition: all 0.5s linear;
  -moz-transition: all 0.5s linear;
  -ms-transition: all 0.5s linear;
  -o-transition: all 0.5s linear;
  transition: all 0.5s linear;
}

.sticky-wrapper.is-sticky .main-menu .nav a:before {
  bottom: 17.6px;
}

/*.sticky-wrapper */

.sticky-dark-bg .header-area:after {
  position: absolute;
  content: "";
  background: rgba(77, 78, 84, 1);
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-transition: all 0.5s linear;
  -moz-transition: all 0.5s linear;
  -ms-transition: all 0.5s linear;
  -o-transition: all 0.5s linear;
  transition: all 0.5s linear;
}

/*.logo*/

.logo a, .logo a:hover, .logo a:focus {
  color: #fff;
  font-size: 24px;
  font-family: "Merriweather Sans", sans-serif;
  text-transform: capitalize;
  font-weight: 600;
  margin: 14px 0px 0;
  padding: 10px 0 0 0;
}

.logo a span {
  color: #00d8ff;
  text-transform: capitalize;
}

/*.logo*/

/*.main-menu*/

.main-menu {
  position: relative;
}

.main-menu ul .nav .navbar-nav {
  text-align: right;
  float: none;
}

.main-menu .nav li {
  position: relative;
  padding: 16px 12px 0px;
  -webkit-transition: all 0.2s linear;
  -moz-transition: all 0.2s linear;
  -ms-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.main-menu .nav li a {
  padding: 11px 0 29px;
  color: #fff;
  font-size: 16px;
  text-transform: capitalize;
  font-family: "Merriweather Sans", sans-serif;
  font-weight: 500;
  -webkit-transition: all 0.25s ease-in-out 0s;
  -moz-transition: all 0.25s ease-in-out 0s;
  -o-transition: all 0.25s ease-in-out 0s;
  transition: all 0.25s ease-in-out 0s;
}

.main-menu .nav li.active a, .main-menu .nav li a:hover, .main-menu .nav li a:focus {
  color: #00d8ff;
  background-color: transparent;
}

.main-menu .nav a:before {
  position: absolute;
  content: "";
  width: 0px;
  height: 2px;
  bottom: -2.4px;
  left: 0;
  background: transparent;
  -webkit-transition: 0.3s ease-in-out;
  -ms-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.main-menu .nav li.active a:before, .main-menu .nav a:hover:before {
  background: #00d8ff;
  width: 100%;
}

a.button-link {
  padding: 0px !important;
}

/*.main-menu*/

/* tooggle */

.main-menu .navbar-toggle {
  margin-top: 5px;
  border: 1px solid;
  font-size: 16px;
  float: left;
}

.main-menu .navbar-toggle {
  color: #00d8ff;
}

.main-menu .navbar-default .navbar-toggle:focus, .main-menu .navbar-default .navbar-toggle:hover {
  background-color: transparent;
}

.home-border {
  border-top: 2px solid #fff;
}

/*book-btn*/

.book-btn {
  background: #00d8ff;
  border: 1px solid #00d8ff;
  width: 120px;
  height: 40px;
  white-space: nowrap;
  text-align: center;
  color: #fff;
  font-size: 14px;
  font-family: "Merriweather Sans", sans-serif;
  text-transform: capitalize;
  border-radius: 5px;
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -ms-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

.book-btn:hover {
  color: #fff;
  background: #00d8d5;
  border: 1px solid #00d8d5;
}

/*book-btn*/

