}

/*about-us-content*/

.about-us-content {
  margin: -175px 0 0 0;
}

.about-us h2 {
  color: #fff;
  font-size: 84px;
  text-transform: inherit;
  max-width: 800px;
  font-weight: 600;
  line-height: 1.22;
}

.about-us h2 span {
  color: #2bdab5;
  margin-top: -11px;
}

.about-us p {
  color: #fff;
  font-size: 16px;
  text-transform: inherit;
  max-width: 800px;
  /* font-weight: 300; */
