  transition: 0.5s ease-in-out;
}

.about-btn:hover .about-view {
  color: #fff;
  background: #00d8d5;
  box-shadow: 0 5px 20px rgba(14, 15, 18, 0.7);
  border: 1px solid #00d8d5;
}

.travel-mrt-0 {
