}

.desc-tabs .nav-tabs>li.active>a {
  color: #fff;
  background: #00d8ff;
  cursor: default;
  font-size: 20px;
  font-weight: 500;
  font-family: "Merriweather Sans", sans-serif;
  text-transform: capitalize;
