  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.single-travel-boxes .nav>li>a:focus, .single-travel-boxes .nav>li>a:hover {
