/* campaign-timer */

.campaign-timer {
  background: rgba(50, 55, 63, 0.65);
  max-width: 555px;
  margin: 43px auto;
  padding: 24px 0 30px;
  border-radius: 5px;
  box-shadow: 0 0 20px rgba(34, 31, 31, 0.2);
}

.camp {
  display: block;
  font-size: 14px;
  color: #fff;
  font-weight: 600;
  font-family: "Merriweather Sans", sans-serif;
  padding: 10px 0;
}

.time-after {
  position: relative;
}

